body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#formDiv{display:flex;flex-direction:column;border-radius:15px;margin:2em;background-color:#D14344;width:50%}#formDiv h3{font-family:"Fredoka One",cursive;color:white;font-size:30px;text-shadow:4px 4px #00000046}#formDiv .jobForm{font-family:"Varela Round",sans-serif;display:flex;margin:2em;flex-direction:column}#formDiv .jobForm label{display:flex;flex-direction:column;width:100%;font-size:20px;margin-bottom:1.5em;color:white}#formDiv .jobForm input{height:2.5em}#formDiv .jobForm #addJobButton{font-family:"Varela Round",sans-serif;cursor:pointer;color:#D14344;font-size:16px;text-transform:uppercase;width:50%;text-align:center;border:0;padding:10px 0;border-radius:5px}#formDiv .jobForm #addJobButton:hover{background-color:#292936}

.myjob-table{font-family:"Varela Round",sans-serif;display:flex;flex-direction:column;justify-content:flex-start;border-radius:15px;margin:2em;background-color:#D14344;width:50%}.myjob-table table{margin:1em;background-color:#F9F9F9;width:95%;border-collapse:collapse;border-radius:15px;border-spacing:20px 20px}.myjob-table tbody{background-color:#F9F9F9}.myjob-table #jobRow{background-color:#F9F9F9;box-shadow:5px 5px 5px rgba(52,52,69,0.3)}.myjob-table #jobRow td{padding-bottom:.5em;padding-top:.5em}.myjob-table #headings{font-size:1.25em;font-weight:bold}.myjob-table #headings td{text-decoration:underline}.myjob-table h3{font-family:"Fredoka One",cursive;color:white;font-size:30px;text-shadow:4px 4px #00000046}

.dashBoardDiv{display:flex;flex-direction:row}


header{background-image:linear-gradient(to top, #F58638 0%, #D14344 100%)}header h1{font-family:"Fredoka One",cursive;font-size:50px;color:#eee;display:inline-block;text-shadow:4px 4px #00000046}header h4{margin:0;margin-right:4em;text-align:right;color:#F9F9F9}

.modal{bottom:0;left:0;position:absolute;right:0;top:0;z-index:100}.modal:after{background:#000;bottom:0;content:' ';left:0;opacity:.5;position:absolute;right:0;top:0}.modal>div{background:#fff;box-shadow:0 1px 4px rgba(0,0,0,0.3),0 0 40px rgba(0,0,0,0.1) inset;box-sizing:border-box;border-radius:5px;display:flex;flex-direction:column;height:28%;margin:auto;padding:0;position:relative;top:50%;transform:translateY(-50%);width:35%;z-index:1000}.modal>div>header{background:#F58638;border-bottom:0 none;border-top-left-radius:5px;border-top-right-radius:5px;box-shadow:0 1px 5px rgba(0,0,0,0.46);display:flex;font-size:1em;font-weight:bold;height:1.5em;line-height:1.5em;padding:.25em 1em}.modal>div>header span{color:#000;flex:1 1}.modal>div>header button{background:inherit;border:0;cursor:pointer;order:1;text-align:center}.modal>div>header button:hover{background:#000;color:#fff}.modal>div>div{flex:1 1;overflow:auto;padding:25px}

.modal-container{display:flex;flex-direction:column}#description-container{display:flex;flex-direction:row;justify-content:space-between}#description-1{display:flex;flex-direction:column;justify-content:flex-start;align-items:flex-start;align-content:flex-start}#description-2{display:flex;flex-direction:column;justify-content:flex-start;align-items:flex-start;align-content:flex-start}label{margin-top:10px;margin-bottom:3px}span{padding:2px}button:hover{cursor:pointer}p{font-size:14px}#modal-button{width:75px;margin-top:10px}

.jobList-table{font-family:"Varela Round",sans-serif;display:flex;flex-direction:column;border-radius:15px;margin:2em;width:80%}.jobList-table table{border-radius:15px;margin:1em;background-color:#F58638;width:95%;border-collapse:collapse;border-spacing:20px 20px}.jobList-table #jobRow{background-color:#343445;border-bottom:2px solid #F9F9F9;color:#F9F9F9}.jobList-table #jobRow td{padding-bottom:.5em;padding-top:.5em}.jobList-table #headings{font-size:1.25em;font-weight:bold;font-size:20px}h2{font-family:"Fredoka One",cursive;color:#F9F9F9;font-size:40px;text-shadow:4px 4px #00000046}

#nav{background:#F58638;box-shadow:5px 5px 5px rgba(52,52,69,0.3)}#nav ul{list-style-type:none;margin:0;padding:0}#nav ul li{display:inline-block;padding:0.5rem 2rem;position:relative}#nav ul li span{color:#111;text-decoration:none}#nav ul li span:after{bottom:0;content:"";left:0;position:absolute;right:0;top:0}#nav ul li:hover{background:#fff}

.signInDiv{display:flex;width:70%;flex-direction:column;justify-content:end}.signInForm{background-image:linear-gradient(to top, #D14344 0%, #F58638 100%);padding:30px 30px 30px 30px;border-radius:10px;top:50%;left:50%;width:400px;box-shadow:5px 10px 10px rgba(52,52,69,0.2)}.signInForm input{font-family:"PT Mono",monospace;border-radius:5px;font-size:16px;background:#F9F9F9;width:100%;border:0;padding:10px 10px;margin:15px -10px}#loginButton{font-family:"Varela Round",sans-serif;cursor:pointer;color:#D14344;font-size:16px;text-transform:uppercase;width:50%;text-align:center;border:0;padding:10px 0;border-radius:5px}#loginButton:hover{background-color:#292936}.loginSwitch{display:inline-block;transition-duration:0.3s;transition-property:transform;-webkit-tap-highlight-color:transparent;transform:translateZ(0);box-shadow:0 0 1px transparent}.loginSwitch:hover{transform:scale(1.1)}

.signUpDiv{display:flex;width:70%;flex-direction:column;justify-content:end}.signUpForm{background-image:linear-gradient(to top, #D14344 0%, #F58638 100%);padding:30px 30px 30px 30px;border-radius:10px;top:50%;left:50%;width:400px;box-shadow:5px 10px 10px rgba(52,52,69,0.2)}.signUpForm input{font-family:"PT Mono",monospace;border-radius:5px;font-size:16px;background:#F9F9F9;width:100%;border:0;padding:10px 10px;margin:15px -10px}#loginButton{font-family:"Varela Round",sans-serif;cursor:pointer;color:#D14344;font-size:16px;text-transform:uppercase;width:50%;text-align:center;border:0;padding:10px 0;border-radius:5px}#loginButton:hover{background-color:#292936}

h1{font-family:"Varela Round",sans-serif}.splashDiv{width:100%;display:flex;justify-content:center;align-items:center;flex-direction:column}


.App {
  text-align: center;
  background-color: #F9F9F9;
  height: 100%;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

